import React, { Component } from 'react';

class DetailInfo extends Component {

  _renderGenre = () => {
    const genres = this.props.store.selectedMovie.genres.map(genre => {
      return <Genre genre={genre.name} />
    })
    return genres;
  }

  render() {
    return (
      <>
        {/* <h2>{this.props.key}</h2>
        <h4>{this.props.title}</h4>
        <div>{this.props.og_title}</div>
        <div>{this.props.poster}</div>
        <div>{this.props.runtime}</div>
        <div>{this.props.vote_average}</div>
        {this._renderGenre()} */}
      </>
    );
  }
}

function Genre ({genre}) {
  return (
    <span className="Movie__Genre">{genre}</span>
  )
}

export default DetailInfo;